<style scoped>

    .code-img {
        display: inline-block;
        width: 150px;
        margin-left: 20px;
        height: 52px;
        border-radius: 5px;
        background: #1bfffc;
    }

    .code-button {
        margin-left: 20px;
        height: 52px;
        width: 150px;
    }

    .forget-hud {
        display: block;
        width: 333px;
        margin: auto;
        text-align: left;
        padding-top: 20px;
        font-size: 14px;
        color: #363636;
    }
</style>

<template>
    <div class="account-page">
        <h3 class="account-page-title">
            欢迎登录创享受沙龙
        </h3>
        <div class="account-page-btn">
            <div v-show="first">
                <input class="account-page-input" v-model="data.mobile" placeholder="手机号码登录"/>
                <div class="account-page-code" style="margin-top: 20px">
                    <input v-model="data.code" class="account-page-input" style="width: 120px" placeholder="短信验证码"/>
                    <sms-code :phone="data.mobile"></sms-code>
                </div>
                <div class="account-page-code" style="margin-top: 20px">
                    <input v-model="data.verify" class="account-page-input" style="width: 120px" placeholder="图形验证码"/>
                    <verify ref="verify"></verify>
                </div>
            </div>
            <div v-show="!first">
                <input class="account-page-input" type="password" v-model="changeData.password" placeholder="请输入新的密码"/>
                <input class="account-page-input" type="password" v-model="changeData.confirm_pass"
                       style="margin-top: 50px"
                       placeholder="再次输入新的密码"/>
                <span class="forget-hud"> 亲, 请谨记新的账号密码</span>
            </div>
        </div>


        <div class="account-page-bottom">
            <button class="account-page-button" @click="next">下一步</button>
            <div class="account-page-spn">
                <span style="margin-left: 10px;font-size: 14px" @click="$router.push({name:'register'})">注册新用户</span>
            </div>
        </div>
    </div>
</template>

<script>
    import Verify from '../components/verify'
    import SmsCode from '../components/smsCode'
    import api from '../../api/account'

    export default {
        name: "forget",
        components: {
            Verify, SmsCode
        },
        data() {
            return {
                first: true,
                data: {
                    mobile: '',
                    code: '',
                    verify: '',
                },
                changeData: {
                    encrypted_str: '',
                    password: '',
                    confirm_pass: '',
                }
            }
        },
        methods: {
            next() {
                if (this.first) {
                    this.firstTime();
                } else {
                    this.secondTime();
                }
            },
            firstTime() {
                // if (!api.isPoneAvailable(this.data.mobile)) {
                //     this.message('请输入正确的手机号');
                //     return;
                // }
                // if (!api.isStrValid(this.data.code)) {
                //     this.message('请输入短信验证码');
                //     return;
                // }
                // if (!api.isStrValid(this.data.verify)) {
                //     this.message('请输入图形');
                //     return;
                // }
                api.recoverPass(this.data,
                    res => {
                        this.changeData.encrypted_str = res.data.data;
                        this.first = false;
                    },
                    res => {
                        this.$refs.verify.refImg();
                        if (res.data.message) {
                            this.message(res.data.message)
                        }

                    })

            },
            secondTime() {
                if (!api.isStrValid(this.changeData.password)) {
                    this.message('请输入密码');
                    return;
                }
                if (!api.isStrValid(this.changeData.confirm_pass)) {
                    this.message('请输入确认密码');
                    return;
                }
                api.changePass(this.changeData, () => {
                    this.$router.push({
                        name: 'login',
                    });
                    this.$message.success('修改密码成功')

                }, res => {
                    if (res.data.message) {
                        this.message(res.data.message)
                    }
                })

            },
            message(text) {
                this.$message.error(text)
            }

        }
    }
</script>
