import Api from './api'

export default class home {
    static index(callback, errorBack) {
        Api.request('POST', this.uri() + 'index', callback, errorBack, null, null)
    }

    static uri() {
        return Api.url() + 'index/';
    }
    static isStrValid(str) {
        return !(str === undefined || str === null || str.length === 0);
    }
}
