import Api from './api'

export default class dynamicApi {

    static spinner(callback, errorBack) {
        Api.request('POST', this.uri() + 'spinner', callback, errorBack, null, null)
    }

    static list(page, id, callback, errorBack) {
        let params = {jump_page: page, page_size: 6, column_id: id};
        Api.request('POST', this.uri() + 'index', callback, errorBack, params)
    }

    static detail(id, callback, errorBack) {
        let params = {id: id,};
        Api.request('POST', this.uri() + 'detail', callback, errorBack, null, params)
    }

    static uri() {
        return Api.url() + 'dynamic/';
    }

    static isStrValid(str) {
        return !(str === undefined || str === null || str.length === 0);
    }
}
