<template>
    <ul class="pagination" v-if="current">
        <li v-show="current !== 1" @click="goto(1)"><a>首页</a></li>
        <li v-for="index in pages" @click="goto(index)" :class="{'active':current === index}" :key="index">
            <a>{{index}}</a>
        </li>
        <li v-show="allpage !== current && allpage !== 0 " @click="goto(allpage)"><a>最后一页</a></li>
    </ul>
</template>
<script>
    export default {
        name: "pagination",
        props: ['allpage', 'current_page'],
        data() {
            return {
                showItem: 5,
                current: 1,
            }
        },
        watch: {
            current_page(val) {
                this.current = val;
            }
        },
        computed: {
            pages: function () {
                let pag = [];
                if (this.current < this.showItem) { //如果当前的激活的项 小于要显示的条数
                    //总页数和要显示的条数那个大就显示多少条
                    let i = Math.min(this.showItem, this.allpage);
                    while (i) {
                        pag.unshift(i--);
                    }
                } else { //当前页数大于显示页数了
                    let middle = this.current - Math.floor(this.showItem / 2),//从哪里开始
                        i = this.showItem;
                    if (middle > (this.allpage - this.showItem)) {
                        middle = (this.allpage - this.showItem) + 1
                    }
                    while (i--) {
                        pag.push(middle++);
                    }
                }
                return pag
            }
        },
        methods: {
            goto: function (index) {
                if (index === this.current) return;
                this.current = index;
                this.$emit('selectPages', index);
                //这里可以发送ajax请求
            },
        }
    }
</script>

<style scoped>
    li {
        list-style: none;
    }

    a {
        text-decoration: none;
    }

    .pagination {
        position: relative;
        font-size: 14px;
    }

    .pagination li {
        display: inline-block;
        margin: 0 5px;
    }

    .pagination li a {
        padding: .5rem 1rem;
        display: inline-block;
        border: 1px solid #ddd;
        border-radius: 5px;
        background: #fff;
        color: #363636;
    }

    .pagination li a:hover {
        background: #eee;
    }

    .pagination li.active a {
        background: #ff9900;
        color: #fff;
    }
</style>
