        <nav-bar
                :navList="[{index: 'home', name: '产业联盟首页'}, {name: '服务资源', index: 'service'}, {name: '资源详情'}]"></nav-bar>
        <div class="info">
            <div class="detail-info">
                <img class="img" :src="'http://alliance.fh.81youxi.com'+info.picture"/>
                <div class="text">
                    <H3 class="two-line" style="height:54px ">{{info.name}} </H3>
                    <span class="night-line">{{info.description}}</span>
                    <span class="price-div">
                            <span class="price-label">价格:</span>
                            <span class="price-value"> {{info.price}}</span>
                            <span class="price-label">起</span>
                        </span>
                    <button @click="link">立即咨询</button>
                </div>
            </div>

        </div>

        <div style="display: flex">
            <div class="html">
                <div class="nav-item-line"></div>
                <div class="html" v-html="info.rich_text">
                </div>
            </div>
            <div class="list">
                <span style="color: #BCBCBC;width: 50%; display: inline-block;text-align: left">推荐服务</span>
                <span style="color: #BCBCBC;width:50%;display: inline-block;text-align: end"
                      @click="$router.push({name:'service'})">更多>></span>
                <div class="nav-item-line"></div>
                <div style="margin-top: 30px">
                    <div class="service-item-content" v-for="item in list" v-bind:key="item.id"
                         @click="detail(item.id)">
                        <div class="list-img"
                             :style="{'backgroundImage':'url('+'http://alliance.fh.81youxi.com'+item.picture+')'}">
                        </div>
                        <div class="list-span">
                            <div class="two-line list-name">{{item.name}}</div>
                            <span style="font-size: 20px;color: #FF9900;line-height: 30px">{{item.price}}</span>
                            <span style="color: #666;margin-left: 10px">起</span>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Api from '../../api/service'

    export default {
        name: "serviceDetail",
        data() {
            return {
                list: [],
                info: {},
            }
        },
        mounted() {
            this.loadData();
        },
        methods: {
            loadData() {
                Api.detail(this.$route.params.id, res => {
                    this.info = res.data.data.service_info;
                    this.list = res.data.data.recommend;
                })
            },
            link() {
                window.open(this.info.address)
            },
            detail(id) {
                this.$router.push({
                    name: 'serviceDetail',
                    params: {id: id}
                });
                this.loadData(this.$route.params.id);
            }
        }
    }
</script>

