<template>
    <div class="account-page">
        <h3 class="account-page-title">
            欢迎登录创享受沙龙
        </h3>
        <div class="account-page-btn">
            <input class="account-page-input" v-model="phone" placeholder="请输入手机号码"/>
            <input class="account-page-input" v-model="pwd" style="margin-top: 10px" type="password"
                   placeholder="请输入密码"/>
            <div class="account-page-code" style="margin-top: 10px">
                <input class="account-page-input" v-model="smsCode" style="width: 120px" placeholder="短信验证码"/>
                <sms-code :phone="phone"></sms-code>
            </div>
            <div class="account-page-code" style="margin-top: 10px">
                <input class="account-page-input" v-model="verify" style="width: 120px" placeholder="图片验证码"/>
                <Verify ref="verify"></Verify>
            </div>
        </div>
        <div class="account-page-bottom">
            <button class="account-page-button" @click="register">注册</button>
            <div class="account-page-spn">
                <span style="margin-right: 20px;font-size: 14px" @click="$router.push({name:'forget'})">忘了密码?</span>|
                <span style="margin-left: 10px;font-size: 14px"
                      @click="$router.push({name:'login'})">已有账号 , 立即登录</span>
            </div>
        </div>
    </div>
</template>

<script>
    import Verify from '../components/verify'
    import SmsCode from '../components/smsCode'
    import Api from '../../api/account'

    export default {
        name: "login",
        components: {
            Verify, SmsCode
        },
        data() {
            return {
                first: true,
                phone: '',
                pwd: '',
                verify: '',
                smsCode: '',
            }
        },
        methods: {
            register() {
                if (!Api.isPoneAvailable(this.phone)) {
                    this.message('请输入正确的手机号');
                    return;
                }
                if (!Api.isStrValid(this.pwd)) {
                    this.message('请输入密码');
                    return;
                }
                if (!Api.isStrValid(this.verify)) {
                    this.message('请输入图形验证码');
                    return;
                }
                if (!Api.isStrValid(this.smsCode)) {
                    this.message('请输入短信验证码');
                    return;
                }
                let data = {
                    mobile: this.phone,
                    password: this.pwd,
                    verify: this.verify,
                    code: this.smsCode,
                };

                Api.register(data, res => {
                    this.$store.dispatch("login", res.data);
                    this.$router.push({
                        name: 'userInfo',
                        params: {name: this.$store.state.user.mobile}
                    });
                }, () => {
                    this.$refs.verify.refImg();
                })
            },
            message(text) {
                this.$message.error(text);
            }
        }
    }
</script>

<style scoped>


    .code-button {
        margin-left: 20px;
        height: 52px;
        width: 150px;
    }
</style>
