<style scoped lang="less">
    .review {
        text-align: left;
        .html {
        }
        .list {
            width: 470px;
            margin-left: 20px;
            cursor: pointer;
            .title {
                width: 100%;
                padding-right: 20px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                color: #8B969F;
                font-size: 14px;
                line-height: 30px
            }
            .time {
                width: 250px;
                color: #BCBCBC;
                font-size: 14px;
                line-height: 30px
            }
        }
    }

    .link-page {
        display: block;
        font-size: 14px;
    }
</style>

<template>
    <div>
        <nav-bar :navList="[{index: 'home', name: '产业联盟首页'}, {name: '联盟动态', index: 'dynamic'}, {name: '动态详情'}]"></nav-bar>

        <div class="review">
            <div class="html">
                <h2 style="margin-top: 0px">{{detail.title}}</h2>
                <span style="color: #BCBCBC">{{detail.publish_time}}</span>
                <div class="nav-item-line"></div>
                <div class="html" v-html="detail.rich_text">
                </div>
                <div class="nav-item-line"></div>
                <div style="margin: 20px 0">
                    <router-link class="link-page" style="margin-bottom: 10px" v-if="prev!=null"
                                 :to="{name: 'dynamicDetail', params: {id: prev.id}}">
                        <span style="font-weight: 400">上一篇:</span>
                        <span>{{prev.title}}</span>
                    </router-link>
                    <router-link class="link-page" v-if="next!=null"
                                 :to="{name: 'dynamicDetail', params: {id: next.id}}">
                        <span style="font-weight: 400">下一篇:</span>
                        <span>{{next.title}}</span>
                    </router-link>
                </div>

            </div>
        </div>

    </div>
</template>

<script>
    import Api from '../../api/dynamic'

    export default {
        name: "review",
        data() {
            return {
                list: [],
                detail: {},
                prev: null,
                next: null,
            }
        },
        mounted() {
            this.loadData();
        },
        methods: {
            loadData() {
                Api.detail(this.$route.params.id, res => {
                    this.detail = res.data.data.detail;
                    this.list = res.data.data.last_review;
                    this.prev = res.data.data.prev;
                    this.next = res.data.data.next;
                })
            },
        },
        watch: {
            '$route'() {
                if (this.$route.name === 'dynamicDetail') {
                    this.loadData();
                }
            }
        }
    }
</script>
