<template>
    <div class="account-page">
        <h3 class="account-page-title">
            欢迎登录创享受沙龙
        </h3>
        <div class="account-page-btn">
            <input class="account-page-input" v-model="phone" placeholder="手机号码登录"/>
            <input class="account-page-input" v-model="pwd" style="margin-top: 20px" placeholder="请输入密码"
                   type="password"/>
            <div class="account-page-code" style="margin-top: 20px">
                <input class="account-page-input" style="width: 120px" v-model="verify" placeholder="图形验证码"/>
                <verify ref="verify"></verify>
            </div>
        </div>
        <div class="account-page-bottom">
            <button class="account-page-button" @click="login">登录</button>
            <div class="account-page-spn">
                <span style="margin-right: 20px;font-size: 14px" @click="$router.push({name:'forget'})">忘了密码?</span>|
                <span style="margin-left: 10px;font-size: 14px" @click="$router.push({name:'register'})">注册新用户</span>
            </div>
        </div>
    </div>
</template>

<script>
    import Verify from '../components/verify'
    import Api from '../../api/account'

    export default {
        name: "login",
        components: {
            Verify
        },
        data() {
            return {
                phone: null,
                pwd: null,
                verify: null,
            }
        },
        mounted() {
            this.$store.dispatch("logout");
            Api.logout()

        },
        methods: {
            login() {
                if (!Api.isPoneAvailable(this.phone)) {
                    this.message('请输入正确的手机号');
                    return;
                }
                if (!Api.isStrValid(this.pwd)) {
                    this.message('请输入密码');
                    return;
                }
                if (!Api.isStrValid(this.verify)) {
                    this.message('请输入图形验证码');
                    return;
                }
                let data = {
                    mobile: this.phone,
                    password: this.pwd,
                    verify: this.verify,
                };

                Api.login(data, res => {
                    this.$store.dispatch("login", res.data);
                    this.$router.push({
                        name: 'userInfo',
                        params: {name: this.$store.state.user.mobile}
                    })
                }, () => {
                    this.$refs.verify.refImg();
                })

            },
            message(text) {
                this.$message.error(
                    text,
                );
            }
        },

    }
</script>

