<style scoped>
    .page {
        padding: 0 30px;
        color: #363636;
    }

    .label {
        display: inline-block;
        width: 150px;
        text-align: right;
        line-height: 60px;
        margin-right: 10px;
    }

    .info-item {

    }

    .input {
        width: 50%;
        font-size: 16px;
    }

</style>

<template>
    <div class="page">
        <h4>基本资料</h4>

        <div class="info-item"><span class="label">公司名称:</span>
            <el-input class="input" :disabled="disable" v-model="data.company" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">企业所处阶段:</span>
            <el-select :disabled="disable" class="input" v-model="data.stage" placeholder="请选择">
                <el-option
                        v-for="item in stages"
                        :key="item.value"
                        :label="item.value"
                        :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div class="info-item"><span class="label">公司网址:</span>
            <el-input class="input" :disabled="disable" v-model="data.website" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">所属行业:</span>
            <el-select :disabled="disable" class="input" v-model="data.industry" placeholder="请选择">
                <el-option v-for="item in industries" :key="item.value" :label="item.value"
                           :value="item.value"></el-option>
            </el-select>
        </div>
        <div class="info-item"><span class="label">企业法人/负责人:</span>
            <el-input :disabled="disable" class="input" v-model="data.legal_person" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">职位:</span>
            <el-input :disabled="disable" class="input" v-model="data.duty" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">手机:</span>
            <el-input :disabled="disable" class="input" v-model="data.mobile" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">单位座机:</span>
            <el-input :disabled="disable" class="input" v-model="data.phone" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">公司邮箱:</span>
            <el-input :disabled="disable" class="input" v-model="data.email" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">企业注册地址:</span>
            <el-input :disabled="disable" class="input" v-model="data.register_address" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">营业执照编号:</span>
            <el-input :disabled="disable" class="input" v-model="data.business_license" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">注册资金:</span>
            <el-input :disabled="disable" class="input" v-model="data.register_capital" placeholder="请输入"/>
        </div>

        <div class="info-item flex"><span class="label">营业执照扫描件:<br/><span
                style="font-size: 14px;color: #b5b5b5">必须加盖公章</span></span>
            <img-upload v-model="license_scan" :url="this.data.license_scan"></img-upload>
        </div>
        <div class="info-item"><span class="label">邮寄地址:</span>
            <el-input :disabled="disable" class="input" v-model="data.mailing_address" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">上年度营销额:</span>
            <el-input :disabled="disable" class="input" v-model="data.sales_volume" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">公司总人数:</span>
            <el-input :disabled="disable" class="input" v-model="data.total_people" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">研发人员数量:</span>
            <el-input :disabled="disable" class="input" v-model="data.developer_people" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">专利及著作权:</span>
            <el-input :disabled="disable" class="input" v-model="data.patent" placeholder="请输入"/>
        </div>
        <div class="info-item"><span class="label">高新技术企业:</span>
            <!--<el-input class="input" v-model="data.high_technology" placeholder="请输入"/>-->
            <el-radio-group :disabled="disable" v-model="data.high_technology">
                <el-radio-button label="0">否</el-radio-button>
                <el-radio-button label="1">是</el-radio-button>
            </el-radio-group>
        </div>
        <div class="info-item"><span class="label">期望服务方向:</span>
            <el-select :disabled="disable" class="input" v-model="data.service_direction" placeholder="请选择">
                <el-option
                        v-for="item in service"
                        :key="item.value"
                        :label="item.value"
                        :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div class="info-item flex" style="margin-top: 10px"><span class="label">主要产品介绍:<br/><span
                style="font-size: 14px;color: #b5b5b5">不少于300字</span></span>
            <el-input :disabled="disable" class="input" :autosize="{ minRows: 5 }" type="textarea"
                      v-model="data.products_introduce"
                      placeholder="请输入"/>
        </div>
        <div class="info-item flex" style="margin-top: 20px;margin-bottom: 20px"><span class="label">主营业务介绍:<br/><span
                style="font-size: 14px;color: #b5b5b5">不少于300字</span></span>
            <el-input :disabled="disable" class="input" :autosize="{ minRows: 5 }" type="textarea"
                      v-model="data.business_introduce"
                      placeholder="请输入"/>
        </div>
        <div class="info-item flex"><span class="label">公司logo:</span>
            <img-upload v-model="logo" :url="this.data.logo"></img-upload>
        </div>

        <div class="info-item" style="width: 300px;text-align: right">
            <button v-show="disable" style="margin-top: 30px" class="user-submit" @click="editEd"> 编辑</button>
            <button v-show="!disable" style="margin-top: 30px" class="user-submit" @click="update"> 提交</button>
            <button style="margin-top: 30px;margin-left: 30px" class="user-cancel" @click="cancel"> 取消</button>
        </div>
    </div>
</template>

<script>
    import api from '../../api/user'
    import type from '../../assets/type'
    import imgUpload from '../../components/imgUpload'

    export default {
        name: "userInfo",
        components: {imgUpload},
        data() {
            return {
                user: {},
                disable: true,
                userData: {},
                stages: [{value: '初创'}, {value: '首轮融资'}, {value: '二轮融资'}, {value: '三轮融资'}, {value: '准备上市'}, {value: '创业板上市'}, {value: '中小板上市'}, {value: '主板上市'}, {value: '国外上市'}],
                value: null,
                role: [],
                industries: [{value: '硬件'}, {value: '软件'}, {value: '应用'}, {value: '服务'}, {value: '其他'}],
                service: [{value: '银行贷款'}, {value: '股权融资'}, {value: '新三板咨询与培训'}, {value: '政府扶持资金'}, {value: '应用示范工程'}, {value: '企业沙龙论坛 '}, {value: '专利技术申请'}, {value: '高新企业申请'}, {value: '其他'}],
                data: {},
                logo: null,
                license_scan: null,

            }
        },

        mounted() {
            this.loadData();
            this.role = type.role;
            // this.disable = false;
            this.industry = type.industry;
        },
        methods: {
            loadData() {
                let _this = this;
                api.info(res => {
                    this.data = res.data.data;
                    if (this.data.auditor === 0) {
                        this.$confirm('您的账号尚未完成实名认证,现在开始填写信息,你还要继续吗？', '提示', {
                            confirmButtonText: '继续',
                            cancelButtonText: '取消',
                            type: 'warning'
                        }).then(() => {
                            _this.disable = false;
                        }).catch(() => {
                        });
                    }

                }, () => {
                })

            },
            editEd() {
                this.disable = false;
            },
            update() {
                if (!api.isStrValid(this.data.company)) {
                    this.$message.error('请输入公司名称');
                    return;
                }
                if (!api.isStrValid(this.data.stage)) {
                    this.$message.error('请选择企业所处阶段');
                    return;
                }
                if (!api.isStrValid(this.data.website)) {
                    this.$message.error('请输入公司网址');
                    return;
                }
                if (!api.isStrValid(this.data.industry)) {
                    this.$message.error('请选择所属行业');
                    return;
                }
                if (!api.isStrValid(this.data.legal_person)) {
                    this.$message.error('请输入企业法人/负责人');
                    return;
                }
                if (!api.isStrValid(this.data.duty)) {
                    this.$message.error('请输入职务');
                    return;
                }
                if (!api.isStrValid(this.data.mobile)) {
                    this.$message.error('请输入手机');
                    return;
                }
                if (!api.isStrValid(this.data.phone)) {
                    this.$message.error('请输入单位座机');
                    return;
                }
                if (!api.isStrValid(this.data.email)) {
                    this.$message.error('请输入公司邮箱');
                    return;
                }
                if (!api.isStrValid(this.data.register_address)) {
                    this.$message.error('请输入企业注册地址');
                    return;
                }
                if (!api.isStrValid(this.data.business_license)) {
                    this.$message.error('请输入营业执照编号');
                    return;
                }
                if (!api.isStrValid(this.data.register_capital)) {
                    this.$message.error('请输入注册资金');
                    return;
                }
                if (this.license_scan == null) {
                    this.$message.error('请选择营业执照扫描件');
                    return;
                }
                if (!api.isStrValid(this.data.mailing_address)) {
                    this.$message.error('请输入邮寄地址');
                    return;
                }
                if (!api.isStrValid(this.data.sales_volume)) {
                    this.$message.error('请输入上年度营销额');
                    return;
                }
                if (!api.isStrValid(this.data.total_people)) {
                    this.$message.error('请输入公司总人数');
                    return;
                }
                if (!api.isStrValid(this.data.developer_people)) {
                    this.$message.error('请输入研发人员数量');
                    return;
                }
                if (!api.isStrValid(this.data.patent)) {
                    this.$message.error('请输入专利及著作权');
                    return;
                }
                if (!api.isStrValid(this.data.high_technology)) {
                    this.$message.error('请输入高新技术企业');
                    return;
                }
                if (!api.isStrValid(this.data.service_direction)) {
                    this.$message.error('请选择期望服务方向');
                    return;
                }
                if (!api.isStrValid(this.data.products_introduce)) {
                    this.$message.error('请输入主要产品介绍');
                    return;
                }
                if (!api.isStrValid(this.data.business_introduce)) {
                    this.$message.error('请输入主营业务介绍'
                    );
                    return;
                }
                if (this.logo == null) {
                    this.$message.error('请选择公司logo');
                    return;
                }
                let data = api.objToFormData(this.data);

                data.append('logo',this.logo);
                data.append('license_scan',this.license_scan);
                api.updateInfo(data, () => {
                    this.$message.success('保存成功');
                    this.disable = true;
                });
            },
            cancel() {
                this.disable = true;
                this.setData();
            },
        }
    }
</script>

