<template>
    <div class="review">
        <nav-bar :navList="navs"></nav-bar>
        <div
                v-for="(news,index) in list.data" v-bind:key="index"
                class="item zoomIn animated wow"
                :style="{'animation-delay':(index*2)/20+'s'}"
                v-on:click="detail(news.id)">
            <el-col :span="6">
                <img class="inline-block" :src="'http://alliance.fh.81youxi.com'+news.picture" width="100%"
                     height="200px"/>
            </el-col>
            <el-col :span="16">
                            <span class="item-title one-line">
                                <span style="cursor: pointer" @click="to(news.column_id)">
                                    [{{ news.name }}]
                                </span>
                                {{news.title}}</span>
                <span class="item-description three-line">{{news.description}}</span>
                <span class="item-time flex"><i class="icon iconfont icon-shizhong"></i>
                    <span style="margin-left: 10px"> {{ news.create_time}}</span>
                </span>
            </el-col>
        </div>

        <pagination v-if="this.list.last_page!=1" v-on:selectPages="loadDynamic"
                    :current_page="this.list.current_page"
                    :allpage="this.list.last_page"></pagination>
    </div>
</template>

<script>
    import Api from '../../api/dynamic'
    import pagination from '@/components/pagination.vue'

    export default {
        name: "reviewList",
        components: {
            pagination
        },
        data() {
            return {
                list: [],
                navs: [{index: 'home', name: '产业联盟首页'}, {name: '联盟动态'}]
            }
        },
        mounted() {
            this.loadDynamic(1);
        },

        methods: {
            detail(id) {
                console.log("12121")
                this.$router.push({name: 'dynamicDetail', params: {id: id}})
            },
            to(id) {
                this.$router.push({
                    name: 'dynamic',
                    query: {
                        column_id: id,
                    }
                });
                this.loadDynamic(1);
            },
            loadDynamic(pageIndex) {
                Api.list(pageIndex, this.$route.query.column_id,
                    res => {
                        this.list = res.data.data;
                        window.scroll(0, 0)
                    },
                )
            }
        },
        watch: {
            '$route.query.column_id'() {
                this.loadDynamic();
            }
        }
    }
</script>

