<style scoped>
    .page {
        padding: 0 30px;
        color: #363636;
    }

    .form-title {
        background: #fafafa;
        height: 60px;
        border: 1px solid #eaeaea;
        border-radius: 3px;
    }

    .form-title-item {
        line-height: 60px;
        display: inline-block;
        text-align: left;
        font-weight: bold;
    }

    .form-item {
        height: 60px;
        color: #888888;
        border-bottom: 1px solid #eaeaea;;
    }

    .form-item-span {
        line-height: 60px;
        display: inline-block;
        text-align: left;
    }

    .form-item-span i {
        display: inline-block;
        width: 10px;
        height: 10px;
        margin-right: 5px;
        border-radius: 15px;
    }

    .form-item-status-green {
        background: green;
    }

    .form-item-status-hui {
        background: #c6c6c6;
    }

</style>
<template>
    <div class="page">
        <h4>通知列表</h4>
        <div class="form-title">
            <span class="form-title-item" style="width: 65%;margin-left: 10%">标题信息</span>
            <span class="form-title-item" style="width: 25%">发布时间</span>
        </div>

        <div class="form-item" v-bind:key="item.id" v-for="item in data.data" @click="detail(item)">
            <span class="form-item-span one-line" style="width: 70%;margin-left: 5%">
                <i :class="item.read_status===1?'form-item-status-hui':'form-item-status-green'"></i>{{item.title}}</span>
            <span class="form-item-span one-line" style="width: 25%">{{item.publish_time}}</span>

        </div>
    </div>
</template>

<script>
    import api from '../../api/user'

    export default {
        name: "userMessage",
        data() {
            return {
                data: {},
            }
        },
        mounted() {
            this.loadData(1);
        }
        , methods: {
            loadData(page) {
                api.notificationList(page, res => {
                    this.data = res.data.data;
                })
            },
            detail(item) {
                this.$router.push({
                    name: 'userMessageDetail',
                    params: {
                        id: item.id,
                    }
                })
            }
        }
    }
</script>

