<script>
    // @ is an alias to /src
    import banner from "../components/banner.vue"
    import activity from "../components/activity.vue"
    import service from '../components/services.vue'
    import Api from '../../api/index'

    export default {
        name: 'home',
        components: {
            banner,
            activity,
            service,
        },
        data() {
            return {
                listImg: [
                    {url: "http://activity.fh.81youxi.com/banner.png"},
                ],
                carousel: [],
                services: [],
                dynamic_left: [],
                dynamic_right: [],
                company: [],
                activity: []
            }
        },
        mounted() {
            this.loadData();
            var swiper = new Swiper('.swiper-container', {
                pagination: '.swiper-pagination',
                // paginationClickable: true,
                slidesPerView: 6,
                observer: true,
                observeParents: true,
                autoplayDisableOnInteraction: false,
                nextButton: '.swiper-button-next',
                prevButton: '.swiper-button-prev',
                spaceBetween: 50,
                autoplay: 2000,
                breakpoints: {
                    1024: {
                        slidesPerView: 4,
                        spaceBetween: 40
                    },
                    768: {
                        slidesPerView: 3,
                        spaceBetween: 30
                    },
                    640: {
                        slidesPerView: 2,
                        spaceBetween: 20
                    },
                    320: {
                        slidesPerView: 1,
                        spaceBetween: 10
                    }
                }
            });
        },
        methods: {
            loadData() {
                Api.index(res => {
                        this.carousel = res.data.data.carousel;
                        this.dynamic_left = res.data.data.dynamic_left;
                        this.dynamic_right = res.data.data.dynamic_right;
                        this.company = res.data.data.company;
                        this.activity = res.data.data.activity;
                    }
                )
            },
            apply() {
                if (this.$store.state.user == null) {
                    this.$router.push({name: "login"})
                } else {
                    this.$router.push({name: 'userInfo', params: {name: this.$store.state.user.mobile}})
                }
            },
            detail(id) {
                this.$router.push({name: 'dynamicDetail', params: {id: id}})
            },
            listTo(id) {
                this.$router.push({name: 'dynamic', query: {column_id: id}})
            },
            member() {
                this.$router.push({name: 'member'})
            }
        }
    }
</script>
