import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/home/Home.vue'
import About from './views/About.vue'
import BBS from './views/activity/activityList.vue'
import BBSApply from './views/activity/activityApply.vue'
import BBSDetail from './views/activity/activityDetail.vue'
import Service from './views/service/service.vue'
import ServiceDetail from './views/service/serviceDetail.vue'
import User from './views/user/user.vue'
import UserInfo from './views/user/userInfo.vue'
import UserPwd from './views/user/userPwd'
import userApplyActivity from './views/user/userApplyCom'
import Dynamics from './views/dynamic/dynamics.vue'
import DynamicDetail from './views/dynamic/dynamicDetail.vue'
import Message from './views/user/userMessage'
import UserMessageDetail from './views/user/userMessageDetail'
import Account from './views/account/account'
import Login from './views/account/login'
import Register from './views/account/register'
import Forget from './views/account/forget'
import Member from './views/member/member'

Vue.use(Router);
export default new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home,
            meta: {
                name: 'home',
                keepAlive:true,
            },
        },
        {
            path: '/about',
            name: 'about',
            meta: {
                name: 'about',
                keepAlive:true,

            },
            component: About
        },
        {
            path: '/activity',
            name: 'activity',
            component: BBS,
            meta: {
                name: 'activity',
            },
        },
        {
            path: '/member',
            name: 'member',
            component: Member,
            meta: {
                keepAlive:true,
                name: 'member',
            },
        },
        {
            path: '/activity/:id',
            name: 'activityDetail',
            component: BBSDetail,
            meta: {
                name: 'activity',
            },
        },
        {
            path: '/activity/:id/apply',
            name: 'activityApply',
            component: BBSApply,
            meta: {
                name: 'activity',
            }
        },
        {
            path: '/service',
            name: 'service',
            component: Service,
            meta: {
                keepAlive:true,
                name: 'service',
            }
        },
        {
            path: '/service/:id',
            name: 'serviceDetail',
            component: ServiceDetail,
            meta: {
                name: 'service',
            }
        },
        {
            path: '/dynamic',
            name: 'dynamic',
            component: Dynamics,
            meta: {
                name: 'dynamic',
            }
        },
        {
            path: '/dynamic/:id',
            name: 'dynamicDetail',
            component: DynamicDetail,
            meta: {
                name: 'dynamic',
            }
        },
        {
            path: '/account',
            name: 'account',
            component: Account,
            meta: {
                name: 'account',
            },
            children: [
                {
                    path: 'login',
                    name: 'login',
                    component: Login,
                    meta: {
                        name: 'account',
                    },
                },
                {
                    path: 'register',
                    name: 'register',
                    component: Register,
                    meta: {
                        name: 'account',
                    },
                },
                {
                    path: 'forget',
                    name: 'forget',
                    component: Forget,
                    meta: {
                        name: 'account',
                    },
                }
            ]
        },

        {
            path: '/user/:name',
            name: 'user',
            index: 'user',
            component: User,
            meta: {
                name: 'user',
            },
            children: [
                {
                    path: 'info',
                    name: "userInfo",
                    index: 'user',
                    component: UserInfo,
                    meta: {
                        name: 'user',
                    },
                },
                {
                    path: 'pwd',
                    name: "updatePwd",
                    component: UserPwd,
                    meta: {
                        name: 'user',
                    },
                },
                {
                    path: 'apply/com',
                    name: "userApplyActivity",
                    component: userApplyActivity,
                    meta: {
                        name: 'user',
                    },
                },
                {
                    path: 'message',
                    name: "userMessage",
                    component: Message,
                    meta: {
                        name: 'user',
                    },
                },
                {
                    path: 'message/:id',
                    name: "userMessageDetail",
                    component: UserMessageDetail,
                    meta: {
                        name: 'user',
                    },
                }

            ]
        }
    ]
})
