import Api from './api'

export default class AccountApi {
    static register(data, callback, errorBack) {
        Api.request('POST', this.uri() + 'register', callback, errorBack, data, null)
    }

    static recoverPass(data, callback, errorBack) {
        Api.request('POST', this.uri() + 'recover_pass', callback, errorBack, data, null)
    }

    static changePass(data, callback, errorBack) {
        Api.request('POST', this.uri() + 'change_pass', callback, errorBack, data, null)
    }

    static login(data, callback, errorBack) {
        Api.request('POST', this.uri() + 'login', callback, errorBack, data, null)
    }

    static logout(callback, errorBack) {
        Api.request('POST', this.uri() + 'logout', callback, errorBack, null, null, false)
    }

    static smsCode(phone, callback, errorBack) {
        let params = {
            mobile: phone
        };
        Api.request('GET', Api.url() + 'sms/attain', callback, errorBack, null, params)

    }

    static uri() {
        return Api.url() + 'user/';
    }

    static isPoneAvailable(str) {
        const myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
        return myreg.test(str);
    }

    static isStrValid(str) {
        return !(str === undefined || str === null || str.length === 0);
    }
}
