import * as axios from 'axios';
import store from '../store'
import router from '../router'
import {Message} from 'element-ui'

axios.defaults.withCredentials = true;
export default class Api {
    static domain() {
        // return '/index/';
        return 'http://alliance.fh.81youxi.com/index/';

    }


    static authHeaders() {
        if (store.state.user != null) {
            // noinspection JSAnnotator
            return {
                ContentType: 'application/json',
                // 'access-token':
                'access-token': store.state.user.access_token
            };
        }
    }

    static url() {
        return this.domain();
    }

    static request(method = 'GET', url = '', callback = null, errorBack = null, data = {}, params = {}, isMessage = true) {
        // let time = new Date().getTime();
        axios.request({
            url: url,
            method: method,
            data: data,
            params: params,
            withCredentials: true,
            headers: this.authHeaders(),
        }).then((response) => {
            if (response.data.code != 200) {
                if (isMessage) {
                    Message.error(response.data.message);
                }
                if (response.data.code == 501) {
                    router.push({name: 'login'})
                } else if (errorBack instanceof Function) {
                    errorBack(response);
                }
                return;
            }
            if (callback instanceof Function) {
                callback(response);
            }
        }).catch((error) => {
            if (errorBack instanceof Function) {
                errorBack(error);
            }
        });
    }
}
