<template>
    <div>
        <nav-bar :navList="[{index: 'home', name: '产业联盟首页'}, {name: '联盟活动', index: 'activity'}, {name: '活动报名'}]"></nav-bar>
        <div class="top">
            <div class="top-content">
                <h3>{{detail.title}}</h3>
                <span class="top-item">活动时间:<span class="top-value">{{detail.apply_time}}</span></span><br/>
                <span class="top-item">报名时间:<span
                        class="top-value">{{detail.begin_time}}~{{detail.end_time}}</span></span><br/>
                <span class="top-item">活动地点:<span class="top-value">{{detail.location}}</span></span><br/>
                <span class="top-item">参会人数:<span class="top-value">{{detail.limit==0 ? '无限制': detail.register+'/'+detail.limit}}</span></span><br/>
            </div>
        </div>
        <div class="nav-item">
        <span>
            报名信息
        </span>
            <div class="nav-item-line"></div>
        </div>
        <div class="info">
            <div class="form">
                <label>
                    <span class="name">姓名:</span>
                    <el-input class="value" v-model="userData.username" placeholder="请输入姓名"/>
                </label>
                <label>
                    <span class="name">邮箱:</span>
                    <el-input class="value" v-model="userData.email" placeholder="请输入邮箱"/>
                </label>
                <label>
                    <span class="name">手机:</span>
                    <el-input v-model="userData.mobile" class="value" placeholder="请输入手机"/>
                </label>

                <label>
                    <span class="name" style="width: 120px">公司机构名称:</span>
                    <el-input v-model="userData.company" class="value" placeholder="请输入公司机构名称"/>
                </label>
                <div class="item-select">
                    <span class="name">职位:</span>
                    <el-select class="value" v-model="userData.career" placeholder="请选择">
                        <el-option
                                v-for="item in role"
                                :key="item.id"
                                :label="item.label"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </div>
                <div class="item-select">
                    <span class="name">行业:</span>
                    <el-cascader class="value"
                                 expand-trigger="hover"
                                 :options="industry"
                                 :value="userData.industry"
                                 v-model="userData.industry"
                    >
                    </el-cascader>
                </div>
                <button class="apply" @click="apply">
                    立即报名
                </button>
            </div>
            <div v-if="false" class="text">
                <h3>需要帮助?</h3>
                <span>
                    过铁道时？”我看那边月台的栅栏外有几个卖东西的等着顾客；又看见那肥胖的；一半为了丧事。诸多不便？到南京时！他踌躇了一会；到那边来信；尚不大难；将橘子一股脑儿放在我的皮大衣上；我从北京到徐州。这时我看见他的背影！好在天无绝人之路！但最近两年的不见？家中光景是一日不如一日，托他们直是白托。两脚再向上缩；家中光景是一日不如一日；到这边时，到那边来信！父亲说？
                </span><br/>
                <span class="phone"> 活动咨询：021-45612389</span>
            </div>
        </div>
    </div>


</template>

<script>
    import userApi from '../../api/user'
    import forumApi from '../../api/activity'
    import type from '../../assets/type'

    export default {
        name: "salonApply",
        data() {
            return {
                detail: {},
                user: {},
                role: [],
                industry: [],
                userData: {},
            }
        },
        mounted() {
            this.loadForum();
            this.loadUser();
            this.role = type.role;
            this.industry = type.industry;
        },
        methods: {
            loadForum() {
                forumApi.detail(this.$route.params.id, res => {
                    this.detail = res.data.data;
                    this.status();
                })
            },
            loadUser() {
                userApi.info(res => {
                    this.user = res.data.data;
                    this.setData();
                })
            },
            setData() {
                this.userData = {
                    username: this.user.username,
                    email: this.user.email,
                    mobile: this.user.mobile,
                    company: this.user.company,
                    career: parseInt(this.user.career),
                };
                if (this.user.occupation) {
                    this.userData.industry = [parseInt(this.user.occupation.substring(0, 3)), parseInt(this.user.occupation)];
                }
            },
            apply() {
                if (!forumApi.isStrValid(this.userData.username)) {
                    this.message('error', '请输入姓名');
                    return;
                }
                if (!forumApi.isStrValid(this.userData.email)) {
                    this.message('error', '请输入电子邮箱');
                    return;
                }
                if (!forumApi.isStrValid(this.userData.company)) {
                    this.message('error', '请输入公司名称');
                    return;
                }
                if (!forumApi.isStrValid(this.userData.career)) {
                    this.message('error', '请选择职位');
                    return;
                }
                if (!userApi.isPoneAvailable(this.userData.mobile)) {
                    this.message('error', '请输入正确的手机号');
                    return;
                }
                if (this.userData.industry == null || !forumApi.isStrValid(this.userData.industry[1])) {
                    this.message('error', '请选择行业');
                    return;
                }
                this.userData.occupation = this.userData.industry[1];
                this.userData.id = this.$route.params.id;
                forumApi.apply(this.userData, () => {
                    this.$message.success('报名成功');
                    this.$router.push({
                        name: 'bbsDetail',
                        params: {
                            id: this.$route.params.id
                        }
                    })

                }, res => {
                    this.message('error', res.data.message);
                })

            },
            message(type, text) {
                this.$message.error(text);
            },
        }
    }
</script>

