<style scoped>
    .page {
        padding: 0 30px;
        color: #363636;
    }

    .form-title {
        background: #fafafa;
        height: 60px;
        border: 1px solid #eaeaea;
        border-radius: 3px;
    }

    .form-title-item {
        line-height: 60px;
        display: inline-block;
        width: 25%;
        text-align: center;
        font-weight: bold;
    }

    .form-item {
        height: 60px;
        color: #888888;
        border-bottom: 1px solid #eaeaea;;
    }

    .form-item-span {
        line-height: 60px;
        display: inline-block;
        width: 25%;
        text-align: center;
    }

    .form-item-span i {
        display: inline-block;
        width: 10px;
        height: 10px;
        margin-right: 5px;
        border-radius: 15px;
    }

    .form-item-status-green {
        background: green;
    }

    .form-item-status-hui {
        background: #c6c6c6;
    }

</style>
<template>
    <div class="page">
        <h4>已报名活动</h4>
        <div class="form-title">
            <span class="form-title-item">活动时间</span>
            <span class="form-title-item">名称</span>
            <span class="form-title-item">状态</span>
            <span class="form-title-item">操作</span>
        </div>

        <div class="form-item" v-for="item in list.data" v-bind:key="item.id">
            <span class="form-item-span one-line">{{item.time}}</span>
            <span class="form-item-span one-line">{{item.name}}</span>
            <span class="form-item-span one-line">
                <i :class="item.status==1? 'form-item-status-green':'form-item-status-hui'"></i>{{item.status===1?'已审核':'待审核️'}}</span>
            <span class="form-item-span one-line"
                  style="color: royalblue;font-size: 14px">
                <router-link style="color:royalblue;text-decoration:none"
                             :to="{name:'competition'}">详细</router-link>
                <span @click="cancelBox(item)"> 取消预约</span>
            </span>
        </div>
        <pagination v-if="this.list.last_page!=1" style="width: 100%;text-align: center" v-on:selectPages="loadData"
                    :allpage="this.list.last_page"></pagination>
    </div>
</template>

<script>
    import Api from '../../api/user'
    import pagination from '@/components/pagination.vue'

    export default {
        name: "userPwd",
        components: {
            pagination
        },
        data() {
            return {
                list: [],
            }
        },
        mounted() {
            this.loadData(1)
        },
        methods: {
            loadData(page) {
                Api.competitionApply(page, res => {
                    this.list = res.data.data;
                })
            },
            cancelBox(item) {
                this.$confirm('确定退出' + item.name + '?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.cancel(item.id)
                }).catch(() => {
                });
            },
            cancel(id) {
                Api.competitionCancel({id: id},
                    () => {
                        this.$message.success('取消成功');
                        this.loadData(1);
                    }
                )
            }
        }
    }
</script>

