import Api from './api'

export default class UserApi {

    static index(callback, errorBack) {
        Api.request('POST', this.uri() + 'index', callback, errorBack, null, null)
    }

    static info(callback, errorBack) {
        Api.request('POST', this.uri() + 'info', callback, errorBack, null, null)
    }

    static updateInfo(data, callback, errorBack) {
        Api.request('POST', this.uri() + 'save', callback, errorBack, data, null)
    }

    static modifyPwd(data, callback, errorBack) {
        Api.request('POST', this.uri() + 'change_pass', callback, errorBack, data, null)
    }

    static competitionApply(page, callback, errorBack) {
        let data = {jump_page: page, page_size: 10};
        Api.request('POST', this.uri() + 'apply', callback, errorBack, data, null)
    }

    static competitionCancel(data, callback, errorBack) {
        Api.request('POST', this.uri() + 'cancel', callback, errorBack, data, null)
    }

    static notificationList(page, callback, errorBack) {
        let data = {jump_page: page, page_size: 10};
        Api.request('POST', this.uri() + 'notification', callback, errorBack, data, null)
    }

    static notificationDetail(id, callback, errorBack) {
        let data = {id: id};
        Api.request('POST', this.uri() + 'notification_detail', callback, errorBack, data, null)
    }

    static uri() {
        return Api.url() + 'user/';
    }

    static isPoneAvailable(str) {
        const myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
        return myreg.test(str);
    }

    static isStrValid(str) {
        return !(str === undefined || str === null || str.length === 0);
    }

    static objToFormData(obj) {
        let form = new FormData();
        for (let key in obj) {
            if (obj[key] != null) {
                form.append(key, obj[key]);
            }
        }
        return form;
    }
}
