<template>
    <label>
        <span>
            <span class="upload-img"></span>
            <input type="file" ref="path" accept="image/*" style="display: none" @change="getFile"/>
            <span v-show="picture" class="img-preview">
                <img :src="picture" width="95px" height="95px"/>
            </span>
        </span>
    </label>
</template>

<script>
    export default {
        name: "imgSelect",
        props: ['value', 'url'],
        data() {
            return {
                img: null,
                picture: null,
            }
        },
        watch: {
            url(value) {
                this.picture = 'http://alliance.fh.81youxi.com' + value;
                console.log("-------------")
                console.log( this.picture )
            }
        },
        methods: {
            getFile(e) {
                let file = e.target.files[0];
                if (file != null) {
                    this.img = file;
                    this.imgPreview(file);
                    this.$emit('input', this.img);
                }
                this.$refs.path.value = ''
            },
            imgPreview(file) {
                let self = this;
                if (!file || !window.FileReader) return;
                if (/^image/.test(file.type)) {
                    let reader = new FileReader();
                    reader.readAsDataURL(file);
                    reader.onloadend = function () {
                        self.picture = this.result;
                    };
                }
            },
        }
    }
</script>

<style scoped>
    .upload-img {
        background: url("../assets/imgUpload.png");
        background-size: 100% 100%;
        text-align: center;
        width: 100px;
        display: inline-block;
        height: 100px;
        margin-right: 20px;
    }

    .img-preview {
        background: url("../assets/imgUpload.png");
        background-size: 100% 100%;
        display: inline-block;
        width: 95px;
        padding: 5px;
        height: 95px;
    }

</style>
