<template>
    <div class="nav-item">
        <span v-for="(nav,index) in navList" v-bind:key="index" :style="{'color':nav.index?'#A1A1A1':'#FF9900'}" @click="to(nav)">
             {{nav.name}}
            <span v-show="nav.index">/</span>
        </span>
        <div class="nav-item-line"></div>
    </div>

</template>

<script>
    export default {
        name: "navBar",
        props: ['navList'],
        methods:{
            to(nav){
                if (nav.index){
                    this.$router.push({name:nav.index})
                }
            }
        }
    }
</script>

<style  lang="less">
    .nav-item{
        text-align: left;
        padding-top: 20px;
        padding-bottom: 20px;
        max-width: 1200px;
        margin: auto;
        span{
            font-size: 14px;
            cursor: pointer;
            margin-left: 5px;
            color: #969696;
        }
    }
    .nav-item-line{
        width: 100%;
        height: 1px;
        overflow: hidden;
        margin-top: 5px;
        border: 1px #969696;
        border-bottom-style: dashed;
    }


</style>
