                    <div :class="this.openSelect?'select-div-open':'select-div-close'">
                            <span class="select-item" :class="selectId===item.id ?'select':'' " v-for="item in list"
                                  @click="selectItem(item)" v-bind:key="item.key">{{item.name}}
