        <div>
            <div style="padding: 20px">
                <router-link :to="{name: 'serviceDetail',params: {id: service.id}}"
                             v-for="(service ,index) in services.data"
                             v-bind:key="service.id"
                             class="service-item  zoomIn animated wow"
                             :style="{'animation-delay':(index*2)/20+'s'}">
                    <div class="service-item-content">
                        <img :src="'http://alliance.fh.81youxi.com'+service.picture" width="100%" height="260px">
                        <div class="info">
                            <div class="name one-line">  {{service.name}}</div>
                            <div class="content two-line">  {{service.description}}</div><br/>
                            <div class="price">
                                <div style="width: 50%;display: flex">
                                    <span style="font-size: 20px;color: #FF9900;line-height: 20px">{{service.price}}</span>
                                    <span style="line-height: 20px;margin-left: 10px"> 起</span>
                                </div>
                                <div style="width: 50%;text-align:right">
                                    <a class="link" target="_blank"
                                       :href="service.address">了解详情</a>
                                </div>
                            </div>
                            <br/>
                        </div>
                    </div>

                </router-link>
            </div>
        </div>
        <pagination v-if="this.services.last_page!=1" style="text-align: center" v-on:selectPages="loadService"
                    :allpage="this.services.last_page"
                    :current_page="this.services.current_page"></pagination>
    </div>
</template>

<script>
    // import selectView from '../../components/select.vue'
    import pagination from '@/components/pagination.vue'
    import Api from '../../api/service'

    export default {
        name: "service",
        components: {
          pagination

        },
        data() {
            return {
                selectId: 0,
                list: null,
                openSelect: false,
                options: [{label: "禁用", value: 1}, {label: "正常", value: 2}],
                services: {}
            }
        },
        mounted() {
            this.loadService(1)
        },
        methods: {
            selectItem(item) {
                this.selectId = item.id;
                this.loadService(1);

            },
            loadService(pageIndex) {
                Api.list(pageIndex, this.selectId,
                    res => {
                        this.services = res.data.data.service;
                        if (this.list == null) {
                            this.list = res.data.data.category;
                            this.list.splice(0, 0, {id: 0, name: '全部'});
                        }
                        window.scroll(0, 0)
                    }
                )
            }
        }
    }
</script>
