<style scoped>
    .page {
        padding: 0 30px;
        color: #363636;
    }

    .label {
        display: inline-block;
        width: 120px;
        text-align: right;
        line-height: 60px;
        margin-right: 10px;
    }

    .info-item {

    }

    .input {
        width: 300px;
    }

</style>
<template>
    <div class="page">
        <h4>修改密码</h4>
        <div class="info-item">
            <span class="label">
                账号:
            </span>
            <span>
                {{this.$store.state.user.mobile}}
            </span>
        </div>
        <div class="info-item">
            <span class="label">
                原密码:
            </span>
            <el-input class="input" v-model="user.old_password" type="password" placeholder="请输入"/>
        </div>

        <div class="info-item">
            <span class="label">
                新密码
            </span>
            <el-input class="input" v-model="user.password" type="password" placeholder="请输入"/>
        </div>
        <div class="info-item">
            <span class="label">
                确认密码
            </span>
            <el-input class="input" v-model="user.confirm_pass" type="password" placeholder="请输入"/>
        </div>
        <div class="info-item" style="width: 300px;text-align: right">
            <button style="margin-top: 30px" class="user-submit" @click="updatePwd"> 更新</button>
            <button style="margin-top: 30px;margin-left: 30px" class="user-cancel"> 取消</button>
        </div>
    </div>
</template>

<script>

    import api from '../../api/user'

    export default {
        name: "userPwd",
        data() {
            return {
                user: {
                    old_password: '',
                    password: '',
                    confirm_pass: '',
                },
            }
        },
        methods: {
            updatePwd() {
                if (!api.isStrValid(this.user.old_password)) {
                    this.$message.error('请输入原密码');
                    return;
                }
                if (!api.isStrValid(this.user.password)) {
                    this.$message.error('请输入新密码');
                    return;
                }
                if (!api.isStrValid(this.user.confirm_pass)) {
                    this.$message.error('请再次输入新密码');
                    return;
                }
                api.modifyPwd(this.user, () => {
                        this.$message.success('修改密码成功');
                        this.$router.push({name: 'login'})
                    }
                )
            }
        }
    }
</script>

