import Api from './api'

export default class activityApi {
    static list(page, callback, errorBack) {
        let data = {jump_page: page, page_size: 6};
        Api.request('POST', this.uri() + 'index', callback, errorBack, data, null)
    }

    static detail(id, callback, errorBack) {
        let params = {id: id,};
        Api.request('POST', this.uri() + 'detail', callback, errorBack, params, null)
    }

    static apply(data, callback, errorBack) {
        Api.request('POST', this.uri() + 'apply', callback, errorBack, data, null)

    }

    static uri() {
        return Api.url() + 'activity/';
    }

    static isStrValid(str) {
        return !(str === undefined || str === null || str.length === 0);
    }
}
