<style scoped lang="less">
    .title {
        width: 100%;
        height: 100px;
        text-align: left;
        .bar {
            align-content: center;
            height: 100px;
            display: flex;
            .nav {
                height: 100%;
                display: inline-block;
                color: white;
                text-align: right;
                margin-right: 200px;
                .title-item {
                    position: relative;
                    line-height: 100px;
                    font-size: 14px;
                    cursor: pointer;
                    width: 100px;
                    display: inline-block;
                    text-decoration: none;
                    text-align: center;
                }
                .title-item:hover {
                    .item-active {
                        height: auto;
                    }
                }
                .home-link {
                    color: white;
                }
                .item-active {
                    position: absolute;
                    top: 70px;
                    height: 0;
                    text-align: center;
                    overflow: hidden;
                    background: rgba(36, 36, 36, 1);
                    transition-property: height;
                    transition-duration: 0.8s;
                    transform-style: preserve-3d;
                    .item-active-span {
                        line-height: 40px;
                        display: block;
                        width: 100px;
                        /*padding: 0 20px;*/
                    }
                    .item-active-span:hover {
                        background: rgba(179, 179, 179, 0.5);
                        color: #FFD400;
                    }
                }
            }
            .logo {
                img {
                    height: 50px;
                    margin-left: 80px;
                    margin-top: 25px;
                }
                height: 100%;
                display: inline-block;
            }
        }

        .select {
            color: #FFD400;
        }
    }

    #container {
        width: 400px;
        height: 400px;
        position: relative;
        border: 1px solid #ccc;
    }

    .parent1 {
        /* overall animation container */
        height: 0;
        overflow: hidden;
        transition-property: height;
        transition-duration: 1s;
        perspective: 1000px;
        transform-style: preserve-3d;
    }

    .parent2 {
        /* full content during animation *can* go here */
    }

    .parent3 {
        /* animated, "folded" block */
        height: 56px;
        transition-property: all;
        transition-duration: 1s;
        transform: rotateX(-90deg);
        transform-origin: top;
    }

    /* Hover states to trigger animations */
    #container:hover .parent1 {
        height: 111px;
    }

    #container:hover .parent3 {
        transform: rotateX(0deg);
        height: 111px;
    }
</style>

<template>
    <div class="title"
         :style="{'background-color':($route.name==='home'||$route.meta.name==='account')?this.op:'rgba(54,54,54,1)'}">
        <el-row class="bar">
            <el-col :span="6" class="logo">
                <img src="../assets/logo.png"/>
            </el-col>
            <el-col :span="18" class="nav">
                <div class="inline-block" :key="index" v-for="(item,index) in navList">
                    <router-link v-if="!item.list"
                                 :class="$route.meta.name===item.routeName?'select title-item':'home-link title-item'"
                                 :to="{name:item.routeName}">{{item.label}}
                    </router-link>
                    <div :class="$route.meta.name===item.routeName?'select title-item':'home-link title-item'"
                         v-if="item.list"
                         @click="to('dynamic')">
                        {{item.label}}
                        <div  onClick="event.cancelBubble = true" class="item-active" v-if="item.list">
                            <span class=" home-link item-active-span"
                                  v-bind:key="active.name"
                                  @click="to(active.routeName,active.column_id)"
                                  v-for="active in item.list">
                            {{active.name}}
                             </span>
                        </div>
                    </div>
                </div>
                <div class="inline-block">
                    <router-link :to="{name:'login'}"
                                 :class="$route.meta.name==='account'?'select title-item':'home-link title-item'"
                                 v-show="$store.state.user === null">登录/注册
                    </router-link>
                </div>
                <div class="inline-block">
                    <router-link class="home-link title-item"
                                 v-if="$store.state.user !== null"
                                 :to="{name: 'userInfo', params: {name: this.$store.state.user.mobile}}">
                        {{$store.state.user.mobile}}
                    </router-link>
                </div>
                <div class="inline-block">
                    <router-link class="home-link title-item"
                                 v-show="$store.state.user !== null"
                                 :to="{name:'login'}" @click="logout">注销
                    </router-link>
                </div>

            </el-col>

        </el-row>
    </div>
</template>

<script>
    import api from '../api/dynamic'

    export default {
        name: "titleBar",
        data() {
            return {
                navList: [
                    {label: "首页", routeName: "home"},
                    {
                        label: "联盟动态",
                        routeName: "dynamic",
                        list: []
                    },
                    {label: "联盟成员", routeName: 'member'},
                    {label: "联盟活动", routeName: 'activity'},
                    {label: "服务资源", routeName: "service"},
                    {label: "联系我们", routeName: "about"},
                ],
                op: "rgba(102,102,102,0)",
            }
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
            this.loadData();
        },
        methods: {
            handleScroll() {
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
                let opt = scrollTop / 460;
                this.op = 'rgba(54,54,54,' + opt + ')';
            },
            to(name, id) {
                console.log(this.navList);
                console.log(id);
                this.$router.push({
                    name: name,
                    query: {
                        column_id: id,
                    }
                });
            },
            loadData() {
                let _this = this;
                api.spinner(res => {
                    res.data.data.forEach(value => {
                        _this.navList[1].list.push({
                            name: value.name,
                            routeName: "dynamic",
                            column_id: value.id,
                        })
                    });
                });
            },
            login() {
                // this.$store.dispatch("login", {'name': "chenshengyu", 'token': "sjkhfgdhjgbfhjdsfghjdfghj"})
                this.$router.push({
                    name: 'login'
                })
            }, logout() {
                // Api.logout(()=>{
                //     this.login();
                //     this.$store.dispatch("logout");
                // })
            }, userDetail() {
                this.$router.push({
                    name: 'userInfo',
                    params: {name: this.$store.state.user.name}
                })
            }
        }
    }
</script>

