    .top {
        height: 380px;
        /*margin: 30px 50px;*/
        background: #F2F2F2;
        /*padding-top: 25px;*/
        /*padding-left: 25px;*/
        display: flex;
        .top-item {
            line-height: 40px;
            color: #363636;
        }
        .top-value {
            margin-left: 20px;
        }
        .apply {
            background-color: #FF9900;

        }
        .apply:hover {
            background-color: #ffc101;
        }

        .dis-apply {
            background: #bfbfbf;

        }
    }

    .img {
        width: 547px;
        height: 380px
    }

    .top-content {
        padding-left: 55px;
        text-align: left;
        color: black;
        width: 580px;
        button {
            cursor: pointer;
            width: 200px;
            height: 70px;
            margin-top: 20px;
            border-style: none;
            border-radius: 10px;
