            outline: none;
            font-weight: bold;
            font-size: 18px;
        }
        h3 {
            margin: 35px 0 35px 0
        }
    }

</style>

<template>
    <div>
        <nav-bar :navList="[{index: 'home', name: '产业联盟首页'}, {name: '联盟活动', index: 'activity'}, {name: '活动详情'}]"></nav-bar>
        <div class="top">
            <img class="img" :src="'http://alliance.fh.81youxi.com'+detail.picture"/>
            <div class="top-content">
                <h3 class="one-line" style="font-size: 30px;font-weight: normal">{{detail.title}}</h3>
                <span class="top-item">活动时间:<span class="top-value">{{detail.apply_time}}</span></span><br/>
                <span class="top-item">报名时间:<span
                        class="top-value">{{detail.begin_time}}~{{detail.end_time}}</span></span><br/>
                <span class="top-item">活动地点:<span class="top-value">{{detail.location}}</span></span><br/>
                <span class="top-item">参会人数:<span
                        class="top-value">{{detail.limit==0 ? '无限制': detail.register+'/'+detail.limit}}</span></span><br/>
                <button :class="(this.btnStatus.code===5||this.btnStatus.code===6)?'apply':'dis-apply'" @click="apply">
                    {{btnStatus.btnText}}
                </button>
            </div>
        </div>
        <div class="nav-item">
        <span>
            活动介绍
        </span>
            <div class="nav-item-line"></div>
        </div>
        <div class="html" v-html="detail.rich_text" style="padding: 10px 20px"></div>
    </div>
</template>

<script>
    import Api from '../../api/activity'


    export default {
        data() {
            return {
                detail: {},
                id: '',
                btnStatus: {
                    btnText: '',
                    code: 0,
                },
            }
        },
        mounted() {
            this.id = this.$route.params.id;
            this.loadData();
            // this.status()

        },
        methods: {
            apply() {
                if (this.btnStatus.code === 5) {
                    this.$router.push({name: "login"})
                } else if (this.btnStatus.code === 6) {
                    this.$router.push({name: "bbsApply"})
                }
            }
            ,
            loadData() {
                Api.detail(this.id, res => {
                    this.detail = res.data.data;
                    this.status();
                })
            },
            status() {
                let _this = this;
                switch (this.detail.status) {
                    case 1:
                        this.btnStatus.code = 1;
                        this.btnStatus.btnText = '活动已结束';
                        break;
                    case 2:
                        this.btnStatus.code = 2;
                        this.btnStatus.btnText = '活动未开始';
                        break;
                    case 3:
                        this.btnStatus.code = 3;
                        this.btnStatus.btnText = '报名已结束';
                        ready();
                        break;
                    case 4:
                        this.btnStatus.code = 4;
                        this.btnStatus.btnText = '去报名';
                        if (this.detail.user_status === 1) {
                            this.btnStatus.code = 5;
                        } else if (this.detail.user_status === 2) {
                            this.btnStatus.code = 6;
                        } else {
                            ready();
                        }
                        break;
                    case 5:
                        this.btnStatus.code = 7;
                        this.btnStatus.btnText = '人数已满';
                        ready()
                        break;
                    default:
                        break;
                }

                function ready() {
                    if (_this.detail.user_status === 3) {
                        _this.btnStatus.btnText = '已报名';
                    } else if (_this.detail.user_status === 4) {
                        _this.btnStatus.btnText = '等待审核';
                    }
                }

            }

        }
    }
</script>
