import Api from './api'

export default class ServiceApi {
    static list(page, id, callback, errorBack) {
        let params = {jump_page: page, page_size: 6};
        if (id !== 0) {
            params.category_id = id;
        }
        Api.request('POST', this.uri() + 'index', callback, errorBack, params, null)
    }

    static detail(id, callback, errorBack) {
        let params = {id: id,};
        Api.request('POST', this.uri() + 'detail', callback, errorBack, params, null)
    }

    static uri() {
        return Api.url() + 'service/';
    }
    static isStrValid(str) {
        return !(str === undefined || str === null || str.length === 0);
    }
}
