<template>
    <button :class="'account-page-button code-button '+btnClass" @click="getCode()">{{this.timer
        !==null?'请在'+count+'秒后重试':'获取短信验证码'}}
    </button>
</template>

<script>
    import Api from '../../api/account'

    export default {
        name: "smsCode",
        props: ['phone'],
        data() {
            return {
                timer: null,
                count: 60,
                btnClass: ''
            }
        },
        methods: {
            getCode() {
                if (!this.timer) {
                    if (this.isPoneAvailable(this.phone)) {
                        Api.smsCode(this.phone, () => {
                            this.timerStart();
                        }, res => {
                            if (res.data !== undefined && res.data.message !== undefined) {
                                this.message(res.data.message);
                            }
                        });

                    } else {
                        this.message('请输入正确的手机号')
                    }
                }
            },
            timerStart() {
                this.btnClass = 'dis';
                this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= 60) {
                        this.count--;
                    } else {
                        clearInterval(this.timer);
                        this.timer = null;
                        this.btnClass = '';
                        this.count = '60'
                    }
                }, 1000)
            },

            isPoneAvailable(str) {
                const myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
                return myreg.test(str);
            },
            message(text) {
                this.$message.error(text);
            }
        },

    }
</script>

<style scoped>
    .dis {
        background: #bfbfbf;
    }

    .dis:hover {
        background: #bfbfbf;

    }
</style>
