<template>
    <div>
        <nav-bar :navList="[{index: 'home', name: '产业联盟首页'}, {name: '联盟活动'}]"></nav-bar>
        <activity :list="this.data.data">
        </activity>
        <pagination v-if="data.last_page!=1"
                    :current_page="data.current_page"
                    v-on:selectPages="loadActivity"  :allpage="data.last_page"></pagination>    </div>
</template>

<script>
    import activity from "../components/activity.vue"
    import pagination from '@/components/pagination.vue'
    import Api from '../../api/activity'

    export default {
        components: {
            activity, pagination
        },
        data() {
            return {
                listImg: [
                    {url: "1"},
                    {url: "2"},
                ],
                data: {},

            }
        },
        mounted() {
            this.loadActivity(0);

        },
        methods: {
            loadActivity(pageIndex) {
                Api.list(pageIndex,
                    res => {
                        this.data = res.data.data;
                        window.scroll(0,0)
                    },
                )
            },

        }
    }
</script>

